<template>
  <div>
      <div id="map-container" :style="{height:bodyheight+'px'}"></div>
      <div class="position-fixed top-0 start-0">
        <router-link to="../" tag="span" class="fs-4 bi bi-arrow-left-circle-fill m-2 text-primary rounded-circle shadow-sm"></router-link>
      </div>
      <View-template v-for="(item,key) in data.data" :key="key" :data="item" />
      <slot></slot> 
  </div>
</template>

<script>
import ViewTemplate from "@/components/group/template";
export default {
  components: {
      ViewTemplate,
  },
  props: {
    data:{}
  },
  data(){
        return {
            pageX:0,
            bodyheight:window.innerHeight-200,
        }
    }
}
</script>

<style>

</style>


