<template>
     <div class="text-center border rounded p-3 " :class="{'border-info text-info':data.isActive,'text-black-50':data.isActive==false}" @click="$emit('click')">
        <span class="fs-2" :class="{'text-info':data.isActive,'text-dark':data.isActive==false}">{{data.value}}</span>{{data.unit}}
    </div>
</template>

<script>
export default {
    components: {
    },
    props:{
       data:{},
    },
}
</script>


