<template>
    <component :is="data.type" :data="data.data" @onsubmit="onsubmit"></component>
</template>

<script>
import axios from 'axios'
import prform from "./prform";
import ridingform from "./ridingform";
import scrollList from "./scroll-list";
import passengerShow from "./passenger-show";

import list from "./list";
import test from "./test";

import login from "./login";

import userHead from "./user-head";
export default {
  components: { 
    prform,  
    ridingform,
    scrollList,
    passengerShow,  
    list,
    test, 

    login,  

    userHead,

  },
  props: {
    data:{}
  },
  data() {
    return {
      loading:false,
    };
  },
  methods:{
    onsubmit(key){
        this.loading=true;
        key.url=this.$store.state.apiset.url+key.url;
        axios(key)
          .then(response => {
            this.loading=false;
            let res=response.data;
            if(res.code==1){
              if(res.data){
                if(res.data.userinfo){
                  this.handle_userinfo(res);
                }
                console.log(res);
              }else{
              this.$router.push('/passenger');
              }
             }else{
              console.log(res.msg);
             }
          }).catch(function (error) {this.loading=false;console.log(error)})
    },
    handle_userinfo(res){
      const token = res.data.userinfo.token;
      const user = res.data.userinfo;
      localStorage.setItem('token', token)
      // 每次请求接口时，需要在headers添加对应的Token验证
      axios.defaults.headers.common['Authorization'] = token
      // 更新token
      this.$store.commit('auth_success', token, user);
      let pathurl=this.$route.path;
      pathurl=pathurl.replace(RegExp("/login", "g"),'');
      this.$router.push(pathurl);
    }
  }
}
</script>


