<template>
  <Layout :data="data.group" @getinfo="getdata" @oncancel="$emit('isActive')">
    <div class="container-fluid" >
      <div class="row">
        <Listview v-for="(item,index) in data.data" :key="index" :data="item" :type="'itt'" @click="$emit('getinfo',item)"/>
      </div>
    </div>
  </Layout>
</template>

<script>
import axios from 'axios'
import Listview from '../../list/is'
import Layout from "../../layout/layout";
export default {
  components: {
      Layout,
      Listview,
  },
  props: {
    data:{}
  },
  data(){
    return{
      loading:false,
      keywords:"",
    }
  },
  methods:{
      getdata(keywords){
          this.loading=true;
          axios.post(this.$store.state.apiset.url+this.data.group.url+keywords,{city:'湛江市'})
          .then(response => {
              this.$emit('getdata',response.data.data);
          }).catch(function (error) {console.log('请求失败！')})
      },
  }
}
</script>

<style>

</style>



