<template>
    <div class="row align-items-center bg-white bg-transparent" @click="$emit('click')">
        <div class="col-auto "><div class="p-1 pe-0"><Dincons/></div></div>
        <div class="col p-0 pe-2 border-bottom ">
            <strong class="d-block">{{data.name}}</strong>
            <div class="fs-normal">{{data.adname}}-{{data.address}}</div>
        </div>
    </div>
</template>

<script>
import Dincons from '@/bp/d-cursorfill'
export default {
    components: {
        Dincons,
    },
    props:{
       data:{},
    },
}
</script>


