<template>
    <my-scroll :page="page" :on-refresh="onRefresh" :on-pull="onPull">
        <Layout v-if="data" :data="data">
              <Listview v-for="(item,index) in list" :key="index" :data="item" :type="data.type" @click="onpath('prorder_show-'+item.id)" />
        </Layout>
    </my-scroll>
</template>
<script type="text/javascript">
import axios from 'axios'
import myScroll from '@/bp/myScroll'
import Layout from '@/components/layout/list-body'
import Listview from '../list/is'
export default {
  components:{
     myScroll,
     Layout,
     Listview,
  },
  props: {
    data:{}
  },
    mounted(){
         this.pathaxios();
    },
    data(){
        return{
            list:[],
            page:{
                counter:1,  
                pageStart:1,  
                pageEnd:1,  
                total:1
            },
            loading:true,
        }
    },
    methods:{
        onpath(url){
       // console.log(this.$route.path);
          this.$router.push({path:this.$route.path+'/'+url,query:this.form});
        },
        onRefresh(mun){ //刷新回调
            this.page.counter=1;
            this.pathaxios('refresh');
        },
        onPull(mun){ //加载回调
            if(this.page.counter<this.page.total){
                 this.page.counter=2;
                 this.pathaxios('pull');
            }else{
                this.$root.$emit('setState',7)
            }
        },
        pathaxios(loadkey){
           if(this.loading){
           this.loading=false;
           axios.post(this.$store.state.apiset.url+this.data.url+'?token='+this.$store.state.token+'&page='+this.page.counter,{msg:'list'})
            .then(response => {
                let res=response.data;
                this.loading=true;
               if(res.code==1){
                    res=res.data;
                    this.page={
                        counter:res.current_page,
                        pageStart:1,
                        pageEnd:Math.ceil(res.total/res.per_page),
                        total:res.last_page             
                        };
                    if(loadkey=="refresh"){
                            this.list=res.data;
                            this.$root.$emit('setState',3)
                    }else if(loadkey=="pull"){
                            for(let index in res.data){
                            this.list.push(res.data[index])
                            }
                            this.$root.$emit('setState',5)
                    }else{
                            this.list=res.data;
                    }
               }else{

               }
            }).catch(function (error) {this.loading=true;console.log('请求失败！')})
            }
        }, 
    },


}
</script>



