<template>
    <div class="p-2 bg-white position-relative border-bottom shadow-sm">
        <div class="pe-5">
            <div class="input-group input-group-sm">
                <span class="input-group-text">湛江</span>
                <input class="form-control" value="" ref="keyinput" :placeholder="data.placeholder" v-model="keywords" @keyup="$emit('getinfo',keywords)"/>
            </div>
        </div>
        <div class="position-absolute top-0 end-0 p-2"><div  @click="$emit('oncancel')">取消</div></div>
    </div>
</template>

<script>
export default {
  props: {
    data:{},
  },
  data(){
    return{
      keywords:"",
    }
  },
}
</script>

<style>

</style>


