<template>
    <div class="text-center border rounded p-1" :class="{'border-info text-info':isActive,'text-black-50':isActive==false}" @click="$emit('click')">
        {{data.name}}
    </div>
</template>
<script>
export default {
  props: {
    isActive:'',
    data:'',
  }
}
</script>


