<template>
  <component v-if="loading" :is="data.type" :data="data" @getinfo="getinfo"></component>
</template>

<script>
import {loadMP,loadJs} from '@/router/loadMap.js'
import driving from './amap-driving'
export default {
  components: {
      driving,
  },
  props: {
        data:{}
  },
  data(){
    return {
      loading:false,
    } 
  },
  mounted() {
    // 加载高德地图API
    loadJs('amap-demoutils','//a.amap.com/jsapi_demos/static/demo-center/js/demoutils.js').then((e) => {
      loadJs('amap-demoutils2','//webapi.amap.com/maps?v=1.4.6&key=160cab8ad6c50752175d76e61ef92c50&plugin=AMap.Driving,AMap.Geocoder,AMap.ToolBar').then((e) => {
          loadJs('amap-demoutils3','//webapi.amap.com/ui/1.1/main.js?v=1.1.1').then((e) => {
             // console.log(e);
            this.loading = true;
          });
      });
    })
  },
  methods:{
    getinfo(key){
        this.$emit('getinfo',key);
    }
  }
}
</script>


