<template>
  <div class="bg-white shadow-sm rounded p-3 fs-normal" @click="$emit('click')">
    <div class="fw-bold"><span class="ybefore">今天17:25</span><span class="ybefore">1人</span><span class="ybefore">愿拼车</span> <span class="badge rounded-pill bg-info fw-normal">顺风车</span></div>
    <div class="d-flex justify-content-between py-2">
      <div class="bd-highlight">
        <div class="ymerge">{{data.startadd}}</div>
        <div class="ymerge">{{data.endadd}}</div>
      </div>
      <div class="bd-highlight text-center">
        <span class="text-dark fs-4">{{data.price}}</span>元
        <div class="text-sm">含<span class="text-danger">{{data.price}}</span>元接单奖励</div>
      </div>
    </div>
    <div class="d-flex flex-row bd-highlight">
      <div class="p-1 bd-highlight">尾号0135</div>
      <div class="p-1 bd-highlight"><Icons :data="{type:'star-fill',width:16,height:16,viewBox:'0 0 16 16',fill:'#ffc107'}"/>5.0</div>
    </div>
  </div>
</template>
<script>
import Icons from '@/components/icons/icons'

export default {
  components: {
    Icons,
  },
  props: {
    data:{}
  },
}
</script>



