<template>
  <div class="position-fixed top-0 start-0 end-0 bottom-0">
    <div ref="head" v-if="data.head">
      <Layout-head :data="data.head" @getinfo="getinfo"  @oncancel="$emit('oncancel')"/>
    </div>
    <div class="bg-light overflow-auto" :style="{height:bodyheight+'px'}">
      <slot></slot>
    </div>
    <div ref="foot" v-if="data.foot">
      <Layout-foot :data="data.foot"/>
    </div>
  </div>
</template>

<script>
import LayoutHead from "./head/is";
import LayoutFoot from "./foot/is";
export default {
  components: {
      LayoutHead,
      LayoutFoot,
  },
  props:{
    data:{}
  },
  data(){
        return {
            bodyheight:0,
            headheight:0,
            footheight:0,
        }
  },
  mounted(){
    if(this.data.head){
    this.headheight=this.$refs.head.offsetHeight;
    }
    if(this.data.foot){
    this.footheight=this.$refs.foot.offsetHeight;
    }
    this.bodyheight=window.innerHeight-this.headheight-this.footheight;
    const _this = this;
    // 监听浏览器窗口变化
    window.onresize = function() {
      return (() => {
        _this.bodyheight = window.innerHeight-_this.headheight-_this.footheight;
      })();
    };
  },
  methods:{
    getinfo(key){
        this.$emit('getinfo',key);
    }
  }
}
</script>



