<template>
    <div class="text-center border rounded p-3 " :class="{'border-info text-info':isActive,'text-black-50':isActive==false}" @click="$emit('click')">
        <span class="fs-2" :class="{'text-info':isActive,'text-dark':isActive==false}">{{value}}</span>{{unit}}
    </div>
</template>
<script>
export default {
  props: {
    isActive:'',
    value:'',
    unit:'',
  }
}
</script>


