<template>
  <div class="rounded p-2 d-flex align-items-center" @click="$emit('isActive')">
    <div class="d-inline bg-primary rounded-circle p-1 ms-2"><div class="bg-white rounded-circle p-1"></div></div>
    <div class="ps-2" v-if="data.name">你将从<span class="text-primary fw-lighter">{{data.name}}</span>上车</div>
    <div class="ps-2" v-else>{{group.placeholder}}</div>
    <input id="maps_value" type="hidden" :value="data.value"/>
  </div> 
</template>

<script>
import {loadJs} from '@/router/loadMap.js'
import LocationSdk from "./location-sdk";
export default {
  components: {
  },
  props: {
    group:'',
    data:{}
  },
  data(){
    return {
      origin:"",
    }
  },
  watch:{
    $route(){
      this.onorigin();
    },
    data(){
       this.origin=this.data.value;
    },
    origin(){
       this.loadampe();
    }
  },
  created(){
    this.onorigin();
    console.log("start to get location now.");
			let self = this;
			LocationSdk.getLocation({
				success: res => {
					console.log(res);
					self.location = res;
				},
				error: res => {
					console.log("failed to get location:" + res);
					self.location = {
						longitude: "-1",
						latitude: "-1"
					};
				}
			});
  },
  methods:{
       onorigin(){
          this.origin=this.$route.query.origin;
          if(!this.$route.query.origin){
              this.$router.push({query:{"origin":'110.342359,21.141181'}});
              return "";
          }
       },
       loadampe(){
          // 加载高德地图API
          loadJs('amap-demoutils','//a.amap.com/jsapi_demos/static/demo-center/js/demoutils.js').then((e) => {
          loadJs('amap-demoutils2','//webapi.amap.com/maps?v=1.4.6&key=160cab8ad6c50752175d76e61ef92c50&plugin=AMap.Driving,AMap.Geocoder,AMap.ToolBar').then((e) => {
              loadJs('amap-demoutils3','//webapi.amap.com/ui/1.1/main.js?v=1.1.1').then((e) => {
                  this.ampe(this.origin);
              });
          });
          })
       },
       ampe(origin){
           if(origin){
            const _this = this;
            AMapUI.origin=origin.split(",");
            AMapUI.loadUI(['misc/PositionPicker'], function(PositionPicker) {
            var map = new AMap.Map('map-container', {
                zoom: 10,
                center: AMapUI.origin,
                scrollWheel: false
            })
            new PositionPicker({
                mode: 'dragMap',
                map: map,
               // iconStyle:{url:'http://www.ecgam.com/uploads/style/maps/mapsioc.png',size:[20,36],ancher:[10,29]}
            }).on('success', function(positionResult) {
               // console.log(positionResult);
                let address=positionResult.address;
                address=address.replace(positionResult.regeocode.addressComponent.district,'')
                address=address.replace(positionResult.regeocode.addressComponent.city,'')
                address=address.replace(positionResult.regeocode.addressComponent.province,'')
                _this.data.name=address;
                // _this.data.value=positionResult.position.lng+','+positionResult.position.lat;
              //  document.getElementById('maps_address').innerHTML =address;
                document.getElementById('maps_value').value =positionResult.position.lng+','+positionResult.position.lat;
            }).on('fail', function(positionResult) {
                console.log(positionResult);
            }).start();
            map.panBy(0, 1);
           });
        }
       }
   }
}
</script>

<style>

</style>


