<template>
    <component :is="'l-'+data.type" :data="data" @goback="$emit('goback')"></component>
</template>
<script>
import lBody from './l-body'
import lShow from './l-show'
import lRiding from './l-riding'
import lLogin from './l-login'
import lUser from './l-user'
import lAmapShow from './l-amap-show'
export default {
  components: {
      lBody,
      lLogin,
      lRiding,
      lShow,
      lUser,
      lAmapShow,
  },
  props: {
    data:{}
  }
}
</script>

<style>

</style>


