<template>
    <span @click="$emit('isActive')">
       {{data.length?'已有'+data.length+'条':group.placeholder}}
    </span>
</template>

<script>
export default {
  props: {
    group:'',
    data:''
  },
  methods:{
      onvalue(){
        this.$emit('getinfo',this.data);
      },
      ondata(){
          this.$emit('getdata',[{name:'fd',value:1},{name:'fd',value:2}]);
      }
  }
}
</script>

<style>

</style>


