<template>
  <div class="position-fixed top-0 start-0 bottom-0 end-0" style="z-index:9;">
    <Bfixed>
        <div class="position-absolute top-0 end-0">
            <div class="m-2"><Dclose  @click="$emit('onclose')"/></div>
        </div>
        <div class="p-2">
            <Titletext>
                <template v-slot:title>{{data.title}}</template>
                <template v-slot:text>{{data.text}}</template>
            </Titletext>
            <slot></slot>
        </div>
        <Cbutton @click="$emit('onsubmit')">确定</Cbutton>
    </Bfixed>       
  </div>
</template>

<script>
import Bfixed from '@/bp/b-fixed-bottom'
import Dclose from '@/bp/d-close'
import Titletext from '@/bp/title-text'
import Cbutton from '@/bp/c-button-d'
export default {
  components: {
      Bfixed,
      Dclose,
      Titletext,
      Cbutton,
  },
  props: {
    data:{}
  }
}
</script>

<style>

</style>


