<template>
    <component :is="(data.stype?data.stype:'view-name')" :data="data" @getvalue="onvalue" @getinfo="oninfo"  @getdata="ondata" @isActive="$emit('isActive')"></component>
</template>

<script>
import viewName from "./name";
import search from "./search";
import setName from "./set-name";
export default {
  components: {
    viewName,
    search,
    setName,
  },
  props: {
    data:{}
  },
  methods:{
      oninfo(key){
        this.$emit('getinfo',key);
        this.$emit('isActive');
      },
      onvalue(key){
        this.$emit('getvalue',key);
        this.$emit('isActive');
      },
      ondata(key){
          this.$emit('getdata',key);
      }
  }
}
</script>

<style>

</style>


