<template>
  <Layout :data="data" @onclose="$emit('isActive')" @onsubmit="onsubmit">
    <div class="row row-cols-4 g-2">
        <div class="col" v-for="(item,index) in data.data" :key="index" @click="onselect(item.value)">
            <Isunit :data="item" :isActive="value.includes(item.value)" />
        </div>
    </div>
  </Layout>
</template>

<script>
import Layout from '../../layout/bottom'
import Isunit from '@/bp/is-name'
export default {
  components: {
      Layout,
      Isunit,
  },
  props: {
    data:{}
  },
  data(){
    return{
      value:[],
    }
  },
  methods:{
      onsubmit(){
          this.$emit('getvalue',this.value);
      },
      onselect(key){
        //重新渲染
        if(this.value.includes(key)){
            this.value=this.value.filter(function (ele){return ele != key;});
        }else{
            this.value.push(key);
        }
      }
  }
}
</script>

<style>

</style>


