<template>
  <div class="bg-light">
    <div class="position-relative text-center bg-white p-2 shadow-sm fw-normal">
        <div class="position-absolute top-0 start-0"><div class="d-inline" @click="$emit('goback')"><Dback/></div></div>
        {{data.top.title}}
    </div>
    <View-template v-for="(item,key) in data.data" :key="key" :data="item" />
    <slot></slot> 
  </div>
</template>

<script>
import Dback from '@/bp/d-left'
import ViewTemplate from "@/components/group/template";
export default {
  components: {
      ViewTemplate,
      Dback,
  },
  props: {
    data:{}
  }
}
</script>

<style>

</style>


