<template>
  <div>
    <slot name="body"></slot>
    <Bfixed>
        <div class="d-flex justify-content-between text-normal px-3">
          <div class="bd-highlight">
            <div class="ymerge">{{data.startadd}}</div>
            <div class="ymerge">{{data.endadd}}</div>
          </div>
          <div class="bd-highlight text-center">
            <span class="text-dark fs-4">{{data.price}}</span>元
            <div class="text-sm">含<span class="text-danger">{{data.price}}</span>元接单奖励</div>
          </div>
        </div>
        <Cbutton  @click="$emit('onsubmit')">确定同行</Cbutton>
    </Bfixed>
  </div>
</template>

<script>
import Bfixed from '@/bp/b-fixed-bottom'
import Cbutton from '@/bp/c-button-d'
export default {
  components: {
      Bfixed,
      Cbutton,
  },
  props: {
    data:{}
  }
}
</script>


