<template>
    <component :is="(type?type:'view-text')" :group="group" :data="data" @getinfo="oninfo"  @getdata="ondata" @isActive="$emit('isActive')"/>
</template>

<script>
import name from "./name";
import vUnit from "./v-unit";
import roundedPill from "./rounded-pill";
import sCarpooling from "./s-carpooling";
import viewText from "./text";
import password from "./password";
import amapOrigin from "./amap-origin";
import amapDestination from "./amap-destination";
export default {
  components: {
    name,
    vUnit,
    roundedPill,
    viewText,
    password,
    amapOrigin,
    amapDestination,
    sCarpooling,
  },
  props: {
    group:'',
    type:'',
    data:{}
  },
  methods:{
      oninfo(key){
        this.$emit('getinfo',key);
      },
      ondata(key){
          this.$emit('getdata',key);
      }
  }
}
</script>

<style>

</style>


