<template>
   
</template>

<script>
export default {
    props: {
        data:{}
    },
    data(){
        return {
            origin:'',
            destination:'',
        }
    },

   mounted(){
    var driving= this.data.driving.split("|");
    this.origin=driving[0].split(",");
    this.destination=driving[1].split(",");
    window.drivingdata="";
    var map = new AMap.Map("map-container", {
            center: this.origin,
            zoom: 14
        });

        var drivingOption = {
            policy: AMap.DrivingPolicy.LEAST_TIME, // 其它policy参数请参考 https://lbs.amap.com/api/javascript-api/reference/route-search#m_DrivingPolicy
            ferry: 1, // 是否可以使用轮渡
            province: '京', // 车牌省份的汉字缩写  
        }

        // 构造路线导航类
        var driving = new AMap.Driving(drivingOption)
        
        // 根据起终点经纬度规划驾车导航路线
        driving.search(this.origin, this.destination, function(status, result) {
            // result即是对应的驾车导航信息，相关数据结构文档请参考 https://lbs.amap.com/api/javascript-api/reference/route-search#m_DrivingResult
            // console.log(result);
            let routes=result.routes[0];
            window.drivingdata={
                origin:result.origin.lng+','+result.origin.lat,
                destination:result.destination.lng+','+result.destination.lat,
                distance:routes.distance,
                duration:routes.duration,
                tolls:routes.tolls,
                price:(routes.distance*0.001).toFixed(1),
                time:(routes.time/60).toFixed(0),
            }
            if (status === 'complete') {
                if (result.routes && result.routes.length) {
                    // 绘制第一条路线，也可以按需求绘制其它几条路线
                    drawRoute(result.routes[0])
                    //log.success('绘制驾车路线完成')
                }
            } else {
                log.error('获取驾车数据失败：' + result)
            }
        });

        function drawRoute(route) {
            
            var path = parseRouteToPath(route)

            var startMarker = new AMap.Marker({
                position: path[0],
                icon: 'https://webapi.amap.com/theme/v1.3/markers/n/start.png',
                desc: '数据_2',
                map: map
            })

            var endMarker = new AMap.Marker({
                position: path[path.length - 1],
                icon: 'https://webapi.amap.com/theme/v1.3/markers/n/end.png',
                map: map
            })

            var routeLine = new AMap.Polyline({
                path: path,
                isOutline: true,
                outlineColor: '#ffeeee',
                borderWeight: 2,
                strokeWeight: 5,
                strokeColor: '#0091ff',
                lineJoin: 'round'
            })

            routeLine.setMap(map)

            // 调整视野达到最佳显示区域
            map.setFitView([startMarker, endMarker, routeLine])
        }

        // 解析DrivingRoute对象，构造成AMap.Polyline的path参数需要的格式
        // DrivingResult对象结构参考文档 https://lbs.amap.com/api/javascript-api/reference/route-search#m_DriveRoute
        function parseRouteToPath(route) {
            var path = []

            for (var i = 0, l = route.steps.length; i < l; i++) {
                var step = route.steps[i]

                for (var j = 0, n = step.path.length; j < n; j++) {
                    path.push(step.path[j])
                }
            }

            return path
        }
        var interval =setInterval(()=>{
            if(window.drivingdata){
                this.$emit('getinfo',window.drivingdata);
                clearInterval(interval);
            }
        },30) 
                        // setTimeout(()=>{
                //     this.$root.$emit('setState',3)
                // },500)
   },
   methods:{
 
   }
}
</script>

<style>

</style>


