<template>
    <Layout v-if="data" :data="data" @goback="path_back">
      <Tipsloading v-if="loading"/>
      <Tips v-for="(item,key) in tips" :key="key" :data="item" />
    </Layout>
</template>

<script>
import pathaxios from '@/router/axios-ajax'
import Layout from '@/components/layout/install'
import ViewTemplate from "@/components/group/template";
import Tips from "@/components/tips/install";
import Tipsloading from "@/components/tips/loading";
export default {
  // 注册 channel
  components: {
    ViewTemplate,  
    Layout,
    Tips,
    Tipsloading,
  },
  props: {
    datacache:{},
  },
  data() {
    return {
      data:'',
      viewkey:'index',
      tips:[],
      loading:false,
    };
  },
  mounted () {
    this.ispathMatch();
  },
  watch:{
     $route(){
        this.ispathMatch();
     }
  },
  methods:{
    ispathMatch(){
      if(this.$route.params.pathMatch){
      var patharr = this.$route.params.pathMatch.split('/');
      this.viewkey=patharr.pop();
      }else{
      this.viewkey='index';
      }
      this.ispathaxios();
    },
    ispathaxios(){
      if(this.datacache[this.viewkey]){
        this.data='';
        this.data=this.datacache[this.viewkey];
        this.$forceUpdate();
      }else{
        this.pathaxios(this.viewkey);
      }
    },
    pathaxios,
    path_back() {
        if(this.$route.params.pathMatch){
        var patharr = this.$route.params.pathMatch.split('/');
        patharr.pop();
        var pathurl = patharr.join('/');
        this.$router.push('/passenger/' + pathurl);
        }
    },
    path_top(key) {
        this.$router.push('/passenger/' + this.$route.params.pathMatch + '/' + key);
    },
  }
};
</script>


