<template>
    <div class="col bg-white shadow-sm border-bottom border-3 rounded p-4 text-center mx-1" :class="{'border-primary':isActive,'text-black-50':isActive==false}" @click="$emit('click')">
        <div v-if="data.value==1">
            <div class="fw-bold">{{data.title}}</div>
            <div class="bg-info p-3 m-2"></div>
            <div class="fs-normal">拼成人<span class="fs-6 fw-bold" :class="{'text-primary':isActive}">{{data.okfee?data.okfee:'..'}}</span>元</div>
            <div class="fs-normal">未拼成<span :class="{'text-primary':isActive}">{{data.onfee?data.onfee:'..'}}</span>元</div>
        </div>
        <div v-else>
            <div class="fw-bold">{{data.title}}</div>
            <div class="bg-info p-3 m-2"></div>
            <div class="fs-normal">独享价<span class="fs-6 fw-bold" :class="{'text-primary':isActive}">{{data.onfee?data.onfee:'..'}}</span>元</div>
        </div>
    </div>
</template>
<script>
export default {
  props: {
    isActive:'',
    data:{},
  }
}
</script>


