<template>
  <span class="d-flex align-items-center">
      <span class="bg-info rounded-circle d-inline-block me-2 border border-2 border-white" style="width:38px;height:38px;"  @click="$emit('onimg')"></span>
      <div>
        <div class="fw-bold"  @click="$emit('onname')">{{data.name}}</div>
        <div v-if="data.moblie" class="fs-sm" @click="$emit('onmoblie')">{{data.moblie}} <span class="bi bi-pencil-square"></span></div>
      </div>
  </span>
</template>
<script>
export default {
  props: {
    data:{}
  },
}
</script>



