<template>
    <Bfixed>
        <slot name="amap"></slot>
        <div class="position-absolute start-0 end-0 px-4" style="bottom:120px">
            <slot name="carpooling"></slot>
        </div>
        <div class="p-5"></div>
        <div class="d-flex justify-content-around p-2">
            <slot name="setime"></slot>
        </div>
        <div class="d-flex justify-content-between px-5">
            <slot name="people"></slot>
            <slot name="thankfee"></slot>
            <slot name="message"></slot>
        </div>
        <Cbutton  @click="$emit('onsubmit')">确定</Cbutton>
    </Bfixed>
</template>

<script>
import Bfixed from '@/bp/b-fixed-bottom'
import Cbutton from '@/bp/c-button-d'
export default {
  components: {
      Bfixed,
      Cbutton,
  },
  props: {
    data:{}
  },
  methods:{

  }
}
</script>


