<template>
  <Layout :data="data" @onclose="$emit('isActive')" @onsubmit="onsubmit">
    <div class="row row-cols-4 g-2">
        <div class="col" v-for="(item,index) in data.data" :key="index" @click="onselect(index)">
            <Isunit :value="item.value" :unit="data.group.unit"  :isActive="value===index?true:false" />
        </div>
    </div>
  </Layout>
</template>

<script>
import Layout from '../../layout/bottom'
import Isunit from '@/bp/is-unit'
export default {
  components: {
      Layout,
      Isunit,
  },
  props: {
    data:{}
  },
  data(){
    return{
      value:'',
    }
  },
  methods:{
      onsubmit(){
        this.$emit('getinfo',this.data.data[this.value]);
      },
      onselect(index){
        this.value=index;
      }
  }
}
</script>

<style>

</style>


