<template>
    <div>
        <Input :type="data.itype" :group="data.group" :data="info"
         @getinfo="getinfo"
         @isActive="data.isActive===true?data.isActive=false:data.isActive=true"
         />
        <Select v-if="data.data" v-show="data.isActive" :data="data" 
         @getinfo="getinfo" 
         @getvalue="getvalue" 
         @getdata="getdata"
         @isActive="data.isActive===true?data.isActive=false:data.isActive=true"
        />
    </div>
</template>

<script>
import Input from "./set/is";
import Select from "./select/is";
export default {
  components: {
    Input,
    Select,
  },
  props: {
    data:{},
  },
  data(){
      return{
          info:[],
      }
  },
  mounted(){
      //初步取信息
      if(this.data.data){
        for(let x in this.data.data){
            if(this.data.value.includes(this.data.data[x].value)){
                this.info.push(this.data.data[x]);
            }
        }
      }
  },
  methods:{
      getinfo(key){
        //重新渲染
        if(this.info.includes(key)){
            this.info=this.info.filter(function (ele){return ele != key;});
            this.data.value=this.data.value.filter(function (ele){return ele != key.value;});
        }else{
            this.info.push(key);
            this.data.value.push(key.value);
        }
        this.$emit('getinfo',this.data.name,this.data.value);
      },
      getvalue(key){
        this.data.value=key;
        this.info=[]
        if(this.data.data){
          for(let x in this.data.data){
              if(this.data.value.includes(this.data.data[x].value)){
                  this.info.push(this.data.data[x]);
              }
          }
        }
       
        this.$emit('getinfo',this.data.name,this.data.value);
      },
      getdata(key){
        this.data.data=key;
      }
  }
}
</script>

<style>

</style>


