<template>
    <component :is="type?type:'name'" :group="group" :data="data" @getinfo="oninfo" @getdata="ondata" @isActive="$emit('isActive')"></component>
</template>

<script>
import name from "./name";
export default {
  components: {
    name,
  },
  props: {
    group:'',
    type:'',
    data:{}
  },
  methods:{
      oninfo(key){
        this.$emit('getinfo',key);
      },
      ondata(key){
          this.$emit('getdata',key);
      }
  }
}
</script>

<style>

</style>


