<template>
   <div class="container-fluid" :class="data.layout">
    <div class="row" :class="data.class">
      <Listview v-for="(item,index) in data.data" :key="index" :data="item" :type="data.type" @click="onurl(item.url)"/>
    </div>
   </div>
</template>
<script>
import Listview from '../list/is'
export default {
  components: {
    Listview,
  },
  props: {
    data:{}
  },
  methods:{
    onurl(key){
      if(key){
      this.$router.push('/passenger/' + this.$route.params.pathMatch + '/' + key);
      }
    }
  }
}
</script>



