<template>
   <div class="position-fixed top-0 start-0 end-0 bottom-0 d-flex align-items-center justify-content-center text-center">
    <div class="position-fixed top-0 start-0"><span class="fs-5 bi bi-chevron-left m-2"  @click="$emit('goback')"></span></div>
    <div class="bd-highlight">
      <h1 class="fw-light">{{data.top.title}}</h1>
      <p class="lead text-muted">{{data.top.text}}</p>
      <View-template v-for="(item,key) in data.data" :key="key" :data="item" />
      <p class="lead text-muted">同意《联通统一认证服务条款》《法律条款与隐私政策》并授权海大出行取本机号</p>
    </div>
    <slot></slot>
  </div>
</template>

<script>
import ViewTemplate from "@/components/group/template";
export default {
  components: {
      ViewTemplate,
  },
  props: {
    data:{}
  },
  data(){
        return {
            bodyheight:window.screen.height,
        }
    }
}
</script>

<style>

</style>


