<template>
  <router-link :to="data.url" tag="div" class="col htext-info">
    <span class="bi fs-5" :class="'bi-'+data.icons"></span>
    <div>{{data.name}}</div>
  </router-link>
</template>
<script>
export default {
  props: {
    data:{}
  },
}
</script>



