<template>
  <span class="d-flex align-items-center bg-info rounded-pill badge shadow-sm"  @click="$emit('click')">
      <span class="bg-info rounded-circle d-inline-block me-1 border border-2 border-white" style="width:30px;height:30px;" ></span>
      <div class="text-start">
        <div class="fw-bold ">{{data.name}}</div>
        <div v-if="data.text" class="fw-normal">{{data.text}}</div>
      </div>
  </span>
</template>
<script>
export default {
  props: {
    data:{}
  },
}
</script>



