<template>
    <div class="bg-info rounded p-2 d-flex align-items-center" @click="$emit('isActive')">
        <Dicons />
        <div class="ps-2 fw-bold">{{data.name?data.name:group.placeholder}}</div>
    </div>  
</template>

<script>
import Dicons from '@/bp/d-spinner-group'
export default {
  components: {
      Dicons,
  },
  props: {
    group:'',
    data:{}
  },

}
</script>

<style>

