<template>
    <div class="p-2">
        <input type="password" class="form-control" :placeholder="group.placeholder" v-model="data.value"  @keyup="onvalue"/>
    </div>  
</template>

<script>
export default {
  props: {
    group:"",
    data:{}
  },
  methods:{
      onvalue(){
        this.$emit('getinfo',this.data);
      },
      ondata(){
          this.$emit('getdata',[{name:'fd',value:1},{name:'fd',value:2}]);
      }
  }
}
</script>

<style>

</style>


