import axios from 'axios'
export default function(viewkey) {
    if (this.loading == false) {
        this.loading = true;
        console.log(this.$store.state.token);
        axios.get(this.$store.state.apiset.url + '/index/layout/class/' + viewkey + '?token=' + this.$store.state.token)
            .then(response => {
                this.loading = false;
                let res = response.data;
                if (res.code == 1) {
                    if (res.data) {
                        this.datacache[viewkey] = res.data;
                        this.data = this.datacache[this.viewkey];
                    } else {
                        this.$router.push('/passenger');
                    }
                    console.log(res.msg);
                } else if (res.code == 401) {
                    this.$router.push({ path: this.$route.path + '/login' });
                    console.log(res.msg);
                } else if (res.code == 403) {
                    this.$router.push({ path: this.$route.path + '/login' });
                    console.log(res.msg);
                } else {
                    console.log(res.msg);
                }
            }).catch(function(error) {
                this.loading = false;
                console.log(error)
            })
    }
}


