export function loadJs(name, src) {
    return new Promise(function(resolve, reject) {
        if (document.getElementById(name)) { // 只加载一次
            resolve('ok')
            return
        }
        let script = document.createElement("script");
        script.type = "text/javascript";
        script.async = true;
        script.src = src;
        script.id = name;
        script.onerror = reject;
        script.onload = function(su) {
            resolve('ok')
        };
        document.head.appendChild(script);
    });
}

// 异步加载高德地图API
export function loadMP() {
    const mp = new Promise(function(resolve, reject) {
        if (document.getElementById("amap-webapi")) { // 只加载一次
            resolve(AMap)
            return
        }
        window.init = function() {
            resolve(AMap)
        };
        let script = document.createElement("script");
        script.type = "text/javascript";
        script.async = true;
        script.src = "//webapi.amap.com/maps?v=1.4.6&key=160cab8ad6c50752175d76e61ef92c50&plugin=AMap.Driving,AMap.Geocoder,AMap.ToolBar&callback=init";
        script.id = "amap-webapi";
        script.onerror = reject;
        document.head.appendChild(script);
    });
    return Promise.all([mp])
        .then(function(result) {
            return result
        }).catch(e => {
            console.log(e);
        })
}


