<template>
   <component :is="type" :data="data" @click="$emit('click')"></component>
</template>

<script>
import itt from "./itt";
import unit from "./unit";
import driver from "./driver";
import btnImgTit from "./btn-img-tit";
export default {
  components: {
      itt,
      unit,
      driver,
      btnImgTit,
  },
  props: {
    type:"",
    data:{}
  },
}
</script>

<style>

