<template>
    <component :is="(Array.isArray(data.value)?'view-set':'view-enum')" :data="data" @getinfo="$emit('getinfo')"></component>
</template>

<script>
import viewEnum from "./enum";
import viewSet from "./set";
export default {
  components: {
    viewEnum,
    viewSet,
  },
  props: {
    data:{},
  }
}
</script>

<style>

</style>


