<template>
  <Layout :data="data" @onsubmit="onsubmit" @getprice="getprice">
    <template slot="amap">
      <Amap :data="{type:'driving',driving:$route.query.origin+'|'+$route.query.destination}" @getinfo="getprice"/>
    </template>
    <div v-for="(item,index) in data.data" :key="index" :slot="index">
      <Formview :data="item" @getinfo="getinfo"/>
    </div>
  </Layout>
</template>
<script>
import Layout from '../layout/riding-form'
import Formview from '../form/index'
import Amap from '@/bp/amap-is'
export default {
  components: {
      Layout,
      Formview,
      Amap,
  },
  props: {
    data:{},
  },
   data(){
        return {
            form:{},
        }
    },
  methods:{
    onsubmit(){
      for(let x in this.data.data){
      this.form[x]=this.data.data[x].value;
      }
      console.log(this.form);
      this.$emit('onsubmit',{method: 'post',url: '/index/riding',params:{token:this.$store.state.token},data:this.form,});
    },
    getinfo(){
      this.onprice();
    },
    onprice(){
      this.data.data.carpooling.group.data[0].okfee=Number(Number(this.form.price)*0.8+Number(this.form.tolls)+Number(this.data.data.thankfee.value)).toFixed(1);
      this.data.data.carpooling.group.data[0].onfee=Number(this.form.price)+Number(this.form.tolls)+Number(this.data.data.thankfee.value);
      this.data.data.carpooling.group.data[1].onfee=Number(this.form.price)+Number(this.form.tolls)+Number(this.data.data.thankfee.value);
    },
    getprice(key){
      this.form['price']=key.price;
      this.form['tolls']=key.tolls;
      this.form['origin']=this.$route.query.origin;
      this.form['destination']=this.$route.query.destination;
      this.onprice();
    }
  }
}
</script>


