<template>
  <div>
      <Formview v-for="(item,index) in data.data" :key="index" :data="item"/>
      <Cbutton @click="onsubmit">提交</Cbutton>
  </div>
</template>

<script>
import Formview from '../form/index'
import Cbutton from '@/bp/c-button-d'
export default {
  components: {
      Formview,
      Cbutton,
  },
  props: {
    data:{}
  },
  data() {
    return {
      form:{},
    };
  },
  methods:{
    onsubmit(){
      for(let x in this.data.data){
      this.form[x]=this.data.data[x].value;
      }
      this.$emit('onsubmit',{method: 'get',url:this.data.url,params: this.form});
    },
  }
}
</script>


