<template>
    <component :is="data.type?data.type:'name'" :data="data.data" @pushto="pushto" @getinfo="getinfo" @oncancel="$emit('oncancel')"></component>
</template>
<script>
import name from "./name";
import topTitle from "./top-title";
import searchHead from "./search-head";
export default {
  components: {
    name,
    topTitle,
    searchHead,
  },
  props: {
    data:{}
  },
  methods:{
    pushto(url){
      let patharr = this.$route.path.split('/');
      for(let i = 0 ;i<patharr.length;i++){
        if(patharr[i] == "" || typeof(patharr[i]) == "undefined"){
                patharr.splice(i,1);
                i= i-1;
        }
      }
      if(url.indexOf('../')===0){
        patharr.pop();
      }else if(url.indexOf('./')===0){
        patharr.pop();
      }else{
        
      }
      let pathurl = patharr.join('/');
      pathurl='/'+pathurl+'/'+url;
      this.$router.push(pathurl);
    },
    getinfo(key){
      this.$emit('getinfo',key);
    }
  }
}
</script>

<style>

</style>


