<template>
  <Layout :data="data" @onsubmit="onsubmit">
    
  </Layout>
</template>
<script>
import Layout from '../layout/passenger-show'
export default {
  components: {
      Layout,
  },
  props: {
    data:{},
  },
   data(){
        return {
            form:{},
        }
    },
  methods:{
    onsubmit(){
        this.$emit('onsubmit',{url:'/index/riding?token='+this.$store.state.token,data:this.form});
    },
  }
}
</script>


