<template>
    <Common :datacache="datacache"/>
</template>

<script>
import indexJson from '@/json/index.json';
import ridingJson from '@/json/riding.json';
import loginJson from '@/json/login.json';
import userindex from '@/json/user-index.json';

import Common from "./common";
export default {
  // 注册 channel
  components: {
    Common,  
  },
  data() {
    return {
      datacache:{
        index:indexJson,
        riding:ridingJson,
        login:loginJson,
        user:userindex,
        "user-order":userindex,
        "user-info":userindex,
        },
    };
  },
   
};

</script>


