<template>
    <div class="row gx-2">
        <Isunit v-for="(item,index) in group.data" :key="index" :data="item" :isActive="data.value===item.value?true:false" @click="$emit('getinfo',item)" />
    </div>
</template>

<script>
import Isunit from '@/bp/is-carpooling'
export default {
  components: {
      Isunit,
  },
  props: {
    group:'',
    data:{}
  },
}
</script>



