<template>
    <div>
        <Input :type="data.itype" :group="data.group" :data="info"
         @getinfo="getinfo"
         @isActive="data.isActive===true?data.isActive=false:data.isActive=true"
         />
        <Select v-if="data.data" v-show="data.isActive" :data="data" 
         @getinfo="getinfo" 
         @getdata="getdata"
         @isActive="data.isActive===true?data.isActive=false:data.isActive=true"
        />
    </div>
</template>

<script>
import Input from "./enum/is";
import Select from "./select/is";
export default {
  components: {
    Input,
    Select,
  },
  props: {
    data:{},
  },
  data(){
      return{
          info:{},
      }
  },
  created(){
    this.oninfo();
  },
  mounted(){
      //初步取信息
      //this.oninfo();
  },
  methods:{
      oninfo(){
        //重新渲染
        if(this.data.data){
          for(let x in this.data.data){
              if(this.data.data[x].value===this.data.value){
                  this.info=this.data.data[x];
              }
          }
        }else{
          this.info={value:this.data.value}
        }
      },
      getinfo(key){
        //重新渲染
        this.info=key;
        this.data.value=key.value;
        this.$emit('getinfo',this.data.name,key.value);
      },
      getdata(key){
        this.data.data=key;
      }
  }
}
</script>

<style>

</style>


